import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import './index.css'
import moment from 'moment';

var date = new Date();
var today = moment(date.toLocaleString()).format("MM/DD/YYYY");
console.log(today)
var url = 'https://cors-anywhere.herokuapp.com/http://www.cbr.ru/scripts/XML_daily.asp?date_req='+today; // не работает
//var url = 'https://www.cbr-xml-daily.ru/daily.xml'
console.log(url);

const codeValute = [];
const valueValute = [];
const nominalValute = []; 
const dateToday = []; 


class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            //curRef1: " ",// 71.94 RUB - 1 USD
            //curRef2: " ",// 1 RUB - 0.014 USD
            //curRef3: " ",
            //curRef4: " ",
            //reftext1: " ",
            //reftext2: " ",
            codeLValute: ["USD","RUB"],
            valueLValute: [71.94, 1],
            curAmount1: 1.00,
            curAmount2: 1.00,
            curAmount3: 1.00,
            curAmount4: 1.00,
            select1: "Выбрать",
            select2: "Выбрать",
            select3: "Выбрать",
            select4: "Выбрать",
            nominal1: 0,
            nominal2: 0,
            dateValuteToday: dateToday[0],

        };
        
        this.onChangeSelect1 = this.onChangeSelect1.bind(this);
        this.onChangeSelect2 = this.onChangeSelect2.bind(this);
        this.onChangeSelect3 = this.onChangeSelect3.bind(this);
        this.onChangeSelect4 = this.onChangeSelect4.bind(this);
        this.onChangeCurrencies1 = this.onChangeCurrencies1.bind(this);
        this.onChangeCurrencies2 = this.onChangeCurrencies2.bind(this);
        this.onChangeCurrencies3 = this.onChangeCurrencies3.bind(this);
        this.onChangeCurrencies4 = this.onChangeCurrencies4.bind(this);
        this.onClickedBut = this.onClickedBut.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    componentDidMount() {

        var xhr = new XMLHttpRequest();

        xhr.addEventListener("readystatechange", () => {
            if (xhr.readyState === 4) {
                if (xhr.status === 200) {
                    // request succesful
                    
                    var xml = new DOMParser().parseFromString(xhr.responseText,'text/xml')
                    console.log(xml);
                    let Value = xml.getElementsByTagName("Value");
                    let CharCode = xml.getElementsByTagName("CharCode");
                    let Nominal = xml.getElementsByTagName("Nominal");
                    let Data = xml.getElementsByTagName("ValCurs")[0].getAttribute("Date").toString();
                    for(let i = 0; i < CharCode.length;i++){
                        codeValute[i] = (CharCode[i].firstChild.textContent);
                    }
                    for(let i = 0; i < Value.length;i++){
                        valueValute[i] = (Value[i].firstChild.textContent.replace(/,/, '.'));
                    }
                    for(let i = 0; i < Nominal.length;i++){
                        nominalValute[i] = (Nominal[i].firstChild.textContent);
                    }
                    codeValute.push("RUB")
                    valueValute.push("1")
                    nominalValute.push("1")
                    dateToday.push(Data);   
                    codeValute.push("Выбрать")
                    valueValute.push("0")
                    nominalValute.push("0")
                } else {
                    // error
                  console.error(xhr.responseText);
                }
            }
        });
        xhr.open("GET", url, true);
        xhr.send();
        
      }

    onChangeSelect1(event) {
        this.setState({select1: event.target.value});
        // if(event.target.value === "RUB"){
        //     this.curAmount1 = 71.94
        //     this.curAmount2 = 0.014  
        // }
        // else if(event.target.value === "USD"){
        //     this.curAmount1 = 0.014
        //     this.curAmount2 = 71.94  
        // }
        for(let i = 0;i < this.state.codeLValute.length;i++){
            if(event.target.value === this.state.codeLValute[i]){
                this.curAmount1 = parseFloat(this.state.valueLValute[i]);
                return;
            }   
        } 
    } 
    onChangeSelect2(event) {
        this.setState({select2: event.target.value}); 
        // if(event.target.value === "RUB"){
        //     this.curAmount2 = 71.94
        //     this.curAmount1 = 0.014
        // }   
        // else if(event.target.value === "USD"){
        //     this.curAmount2 = 0.014
        //     this.curAmount1 = 71.94 
        // }
        for(let i = 0;i < this.state.codeLValute.length;i++){
            if(event.target.value === this.state.codeLValute[i]){
                this.curAmount2 = parseFloat(this.state.valueLValute[i]);
                return;
            }   
        } 
    }
    onChangeSelect3(event) {
        this.setState({select3: event.target.value});

        for(let i = 0;i < codeValute.length;i++){
            if(event.target.value === codeValute[i]){
                this.curAmount3 = parseFloat(valueValute[i]);
                this.setState({nominal1: nominalValute[i]})
                return;
            }   
        } 
    }
    onChangeSelect4(event) {
        this.setState({select4: event.target.value}); 

        for(let i = 0;i < codeValute.length;i++){
            if(event.target.value === codeValute[i]){
                this.curAmount4 = parseFloat(valueValute[i]);
                this.setState({nominal2: nominalValute[i]})
                return;
            }   
        } 
    }
    onChangeCurrencies1(event) {
        const {value} = event.target;
       
        if(this.state.select1 !== this.state.select2){
            this.curAmount1 = parseFloat(this.curAmount1);
            this.curAmount2 = parseFloat(this.curAmount2);
            this.setState({text1: value})
            this.setState({text2: value * (this.curAmount1/this.curAmount2).toFixed(4)})
        }
        else if(this.state.select1 === this.state.select2){
            this.setState({text1: value})
            this.setState({text2: value * 1})
        }
    }
    onChangeCurrencies2(event) {
        const {value} = event.target;
       
        if(this.state.select1 !== this.state.select2){
            this.curAmount1 = parseFloat(this.curAmount1);
            this.curAmount2 = parseFloat(this.curAmount2);
            this.setState({text2: value})
            this.setState({text1: value * (this.curAmount2/this.curAmount1).toFixed(4)})
            
        }
        else if(this.state.select1 === this.state.select2){
            this.setState({text2: value})
            this.setState({text1: value * 1})
        }
    }
    onChangeCurrencies3(event) {
        const {value} = event.target;

        if(this.state.select3 !== this.state.select4){
            this.setState({text3: value})
            this.setState({text4: value * (this.curAmount3/this.curAmount4).toFixed(4)})
        }
        else if(this.state.select3 === this.state.select4){
            this.setState({text3: value})
            this.setState({text4: value * 1})
        }
    }
    onChangeCurrencies4(event) {
        const {value} = event.target;

        if(this.state.select3 !== this.state.select4){
            this.setState({text4: value})
            this.setState({text3: value * (this.curAmount4/this.curAmount3).toFixed(4)})
            
        }
        else if(this.state.select3 === this.state.select4){
            this.setState({text4: value})
            this.setState({text3: value * 1})
        }
    }
    onClickedBut(event){
        var curRef1 = this.state.select3;
        var curRef2 = this.state.select4;
        this.setState({select3: curRef2})
        this.setState({select4: curRef1})
        var amRef1 = this.curAmount3;
        var amRef2 = this.curAmount4;
        this.curAmount3 = amRef2;
        this.curAmount4 = amRef1;
        var reftext1 = this.state.text3;
        var reftext2 = this.state.text4;
        this.setState({text3: reftext2});
        this.setState({text4: reftext1});
        var refNom1 = this.state.nominal1;
        var refNom2 = this.state.nominal2;
        this.setState({nominal1: refNom2});
        this.setState({nominal2: refNom1});
    }

    onSubmit(event){
        var curRef1 = this.state.select1;
        var curRef2 = this.state.select2;
        this.setState({select1: curRef2})
        this.setState({select2: curRef1})
        var amRef1 = this.curAmount1;
        var amRef2 = this.curAmount2;
        this.curAmount1 = amRef2;
        this.curAmount2 = amRef1;
        console.log(this.curAmount1 + " " + this.curAmount2)
        event.preventDefault();
        var reftext1 = this.state.text1;
        var reftext2 = this.state.text2;
        this.setState({text1: reftext2});
        this.setState({text2: reftext1});
        event.preventDefault();
    }

    render(){
        const {select1,select2,select3,select4,text1,text2,text3,text4} = this.state   
        return(
            <div align="center">
                <form onSubmit={this.onSubmit}>
                    <ul className="nav" >
                        <li align="left" >
                            <label className="algn">Ковертация валюты локально </label>
                            <label >Выберите валюту: </label>
                            <select name="select1" value={select1} onChange={this.onChangeSelect1}>
                                <option value='RUB'>RUB</option>
                                <option value='USD'>USD</option>
                                <option value='Выбрать'>Выбрать</option>
                            </select>
                            <h> </h>
                            <select name="select2" value={select2} onChange={this.onChangeSelect2}>
                                <option value='RUB'>RUB</option>
                                <option value='USD'>USD</option>
                                <option value='Выбрать'>Выбрать</option>
                            </select>
                            <p/>
                            <input type='text' name="text1" value={text1} onChange={this.onChangeCurrencies1}/> 
                            <h> </h>
                            <input type='text' name="text2" value={text2} onChange={this.onChangeCurrencies2}/>   
                            <input className="nobcgkc" type="submit" value="Поменять валюту местами" />
                            <p/>
                            <label className="algn">Ковертация валюты исходя сайта cbr.ru </label>
                            <label >Выберите валюту: </label>
                            <select name="select3" value={select3} onChange={this.onChangeSelect3}>
                            { codeValute.map((e) => {
                                return <option value={e}>{e}</option>;
                                    })} 
                            </select>
                            <h> </h>
                            <select name="select4" value={select4} onChange={this.onChangeSelect4}>
                            { codeValute.map((e) => {
                                return <option value={e}>{e}</option>;
                                    })} 
                            </select>
                                <text className="algn2">{"  Номинал: " + this.state.nominal1 + "                          Номинал: " + this.state.nominal2}</text>
                                
                            <input type='text' name="text3" value={text3} onChange={this.onChangeCurrencies3}/> 
                            <h> </h>
                            <input type='text' name="text4" value={text4} onChange={this.onChangeCurrencies4}/>   
                                <label className="algn">{"Данные собраны на момент: " + dateToday.toString()}</label>
                                <input className="nobcgkc" type="button" value="Поменять валюту местами" onClick={this.onClickedBut} />
                        </li>
                    </ul>
                </form>
            </div>
        )
    }
}


ReactDOM.render(<App/>,document.getElementById('root'))
